import React, { useState, useEffect } from "react";

import { Fullpage } from "./stateful/Fullpage";
import styled from "styled-components";
import Header from "./stateful/Header";
import { Switch, Route } from "react-router-dom";
import Preview from "./stateful/Preview";
import { ModalView } from "./stateful/ModalView";
import { Collection } from "./stateful/Collection";
import { Full16 } from "./stateful/Full16";

const AppWrapper = styled.div``;

interface AppProps {}

function App({}: AppProps) {
  return (
    <AppWrapper>
      <Header />
      <ModalView />

      <Switch>
        <Route exact path="/collection/">
          <Preview />
          <Collection />
        </Route>
        <Route exact path="/details/16">
          <Preview />
          <Full16 />
        </Route>
        <Route exact path="/:page?">
          <Preview />
          <Fullpage />
        </Route>
        <Route>404</Route>
      </Switch>
    </AppWrapper>
  );
}

export default App;
