import React, { useCallback } from "react";
import styled from "styled-components";
import PreviewContainer from "../hooks/usePreviewContainer";

const PreviewDiv = styled.div`
  z-index: 100;
  position: fixed;
  top: 0;
  left: 0;
  height: 100vh;
  width: 100vw;
  display: flex;
  justify-content: center;
  align-items: center;

  video,
  img,
  figure {
    /* height: 100%; */
  }

  .render-artwork,
  figure > div > img {
    width: auto;
    max-height: 100%;
    max-width: 100%;
    object-fit: cover;

    max-height: 90vh;
    max-width: 90vw;
  }
`;

const Overlay = styled.div`
  z-index: 99 !important;
  position: fixed;
  top: 0;
  left: 0;
  height: 100vh;
  width: 100vw;
  background: #00000099;
  cursor: pointer;
`;

const X = styled.button`
  background: none;
  border: 1px solid ${(props) => props.theme.colors.fg};
  color: ${(props) => props.theme.colors.fg};
  position: absolute;
  top: 5px;
  right: 5px;
  visibility: hidden;
  z-index: 100;
`;

const PreviewInner = styled.div`
  z-index: 101;
  position: relative;
  max-height: 90vh;
  max-width: 90vw;
  height: 100%;
  width: auto;
  object-fit: cover;

  display: flex;
  align-items: center;
  justify-content: center;

  &:hover {
    button {
      visibility: visible !important;
    }
  }
`;

const Preview = () => {
  const { image, setImage } = PreviewContainer.useContainer();

  const clear = useCallback(() => {
    setImage(null);
  }, [setImage]);

  const onClick = useCallback(
    (e: React.MouseEvent<HTMLButtonElement>) => {
      e.stopPropagation();
      clear();
    },
    [setImage]
  );

  if (image) {
    return (
      <PreviewDiv>
        <Overlay onClick={clear} />
        <PreviewInner>
          <X onClick={onClick}>✖</X>
          {image}
        </PreviewInner>
      </PreviewDiv>
    );
  }

  return null;
};

export default Preview;
